.Main-container.mobile {
    background-color: #e6e6e6;
    position: relative;
    width: 100vw;
    height: calc(100vw * (640 / 480));
  }
  
  .Main-container.desktop {
    background-color: #e6e6e6;
    position: relative;
    width: calc(100vh * (640 / 480));
    height: 100vh;
  }
  